import React, { Component } from 'react'
import { Switch, Route } from 'react-router-dom'

import PagePdf from '../Page/Pdf'

import RouterContentTransition from './RouterContentTransition'

export default class RouterContent extends Component {
    render() {
        return (
            <Switch>
                <Route path="/pdf" component={PagePdf} />
                <Route path="*">
                    <RouterContentTransition />
                </Route>
            </Switch>
        )
    }
}
